.card {
    width: 45ch;
    height: 45ch;
    background: transparent;
    border-radius: 5px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    transition: box-shadow 0.5s;
    will-change: transform;
    border: 0px solid white;
    margin: 0 auto;
  }
  
