
.trails-main {
  position: relative;
  width: 50vw;
  margin-bottom: -20vw;
  height: 50vw;
  /* overflow: hidden; */
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  transform: rotateZ(225deg)

}

.trails-text {
  position: relative;
  width: 100vw;
  height: 10vw;
  line-height: 80px;
  color: transparent;
  background: white;
  font-size: 5em;
  font-weight: 800;
  text-transform: uppercase;
  will-change: transform;
  overflow: hidden;
}

.trails-clip {
  width:35vw;
  height:100%;
  min-height:400px;
  overflow: hidden;
  min-width: 350px;
}

.trails-text > div {
  /* overflow: hidden; */
}
