body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    width: 45ch;
    height: 45ch;
    background: transparent;
    border-radius: 5px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-transition: box-shadow 0.5s;
    transition: box-shadow 0.5s;
    will-change: transform;
    border: 0px solid white;
    margin: 0 auto;
  }
  
  .card:hover {
    /* box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4); */
  }
  
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

.trails-main {
  position: relative;
  width: 50vw;
  margin-bottom: -20vw;
  height: 50vw;
  /* overflow: hidden; */
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  -webkit-transform: rotateZ(225deg);
          transform: rotateZ(225deg)

}

.trails-text {
  position: relative;
  width: 100vw;
  height: 10vw;
  line-height: 80px;
  color: transparent;
  background: white;
  font-size: 5em;
  font-weight: 800;
  text-transform: uppercase;
  will-change: transform;
  overflow: hidden;
}

.trails-clip {
  width:35vw;
  height:100%;
  min-height:400px;
  overflow: hidden;
  min-width: 350px;
}

.trails-text > div {
  /* overflow: hidden; */
}

.play-button {
	cursor: pointer;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	height: 100px;
	width: 100px;
	background-color: rgba(255, 255, 255, 1);
	border-radius: 50px;
}

.play-button:hover {
	background-color: rgba(255, 255, 255, 0.5);
}

.play-button:after {
    content: "";
    display: block;
    position: absolute;
    top: 35px;
    left: 43px;
    margin: 0 auto;
    border-style: solid;
    border-width: 12.5px 0 12.5px 20px;
    border-color: transparent transparent transparent rgba(0, 0, 0, 1);
    color: black;

}

.close-video::before {
	content: "\2716";
	cursor: pointer;
	position: absolute;
	right: 0;
	top: 0;
	font-size: 20px;
	padding: 20px;
	z-index: 1;
	line-height: 0.7;
	display: block;
	color: #fff;
}

.video-wrapper {
	position: relative;
	padding: 33.35% 0; /* 16:9 */
	height: 0;
}

.gallery {
	margin: 0 auto;
	width: 60%;
	box-shadow: 10px -10px rgb(0,0,0), 20px -20px rgb(255,255,255), 30px -30px rgb(0,0,0), 40px -40px rgb(255,255,255), 50px -50px rgb(0,0,0);
	-webkit-transition: box-shadow 0.15s ease-in-out;
	transition: box-shadow 0.15s ease-in-out;
}

.box-shadow-add {
	box-shadow: 0px 0px rgba(255,255,255, 0), 5px -5px rgb(0,0,0), 10px -10px rgb(255,255,255), 15px -15px rgb(0,0,0);

}



.video-wrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}



@media (max-width: 1320px) {
	.app-sandbox-content {
		padding: 0 20px;
	}
}

.app-sandbox {
	margin: 40px auto;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
}

.app-buttons li {
	display: block;
}

@media (max-width: 768px) {
	.app-header {
		font-size: 20px;
	}

	.app-buttons li {
		display: block;
		margin: 10px 0;
	}

	.app-buttons li + li {
		padding: 0;
	}

	.play-button {
		height: 40px;
		width: 65px;
	}

	.play-button:after {
		top: 11px;
		left: 27px;
		border-width: 8.5px 0 8.5px 12px;
	}

	.close-video::before {
		font-size: 16px;
		padding: 15px;
	}
}

@media (max-width: 1024px) {
	.app .image-gallery,
	.app-sandbox {
		width: 100%;
	}
}

.app-interval-input-group {
	display: table;
}

.app-interval-label {
	display: table-cell;
	vertical-align: middle;
	padding: 6px 12px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1;
	color: #555;
	text-align: center;
	background-color: #eee;
	border: 3px solid #ccc;
	border-right: none;
	border-radius: 4px;
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
}

.app-interval-input {
	-webkit-appearance: none;
	display: table-cell;
	margin: 0;
	padding: 9px;
	border-radius: 5px;
	font-size: 14px;
	border: 3px solid #ccc;
	background: #fff;
	width: 100%;
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
}

input.app-interval-input {
	width: 65px;
}

.app-checkboxes {
	margin-top: 10px;
}

.app-checkboxes li {
	display: block;
}

.image-gallery-right-nav {
	right: -155px;
	font-size: 10em !important;

}
.image-gallery-left-nav {
	left: -125px;
	font-size: 10em !important;
}

.image-gallery-image {
	-webkit-filter: brightness(0%);
	        filter: brightness(0%);
	-webkit-transition: -webkit-filter 0.5s;
	transition: -webkit-filter 0.5s;
	transition: filter 0.5s;
	transition: filter 0.5s, -webkit-filter 0.5s;
}

.brightness-out {
	-webkit-filter: brightness(0%);
	        filter: brightness(0%);

}

.image-gallery-right-nav::before {
	content: "\F363" !important; 
}


.image-gallery-left-nav::before {
	content: "\F361" !important; 
}

.image-gallery-right-nav:hover::before {
    color: #fef262; }

	.image-gallery-left-nav:hover::before {
		color: #fef262; }
.gallery-container {
    /* padding: .9375rem 0; */
}



.gallery-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    max-width: 1200px;
    width: 100%;
    margin: 50px auto;
}

@media (min-width: 20em) {
    .gallery-grid {
        grid-template-columns: repeat(1, 1fr);
    }
}

@media (min-width: 34em) {
    .gallery-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 60em) {
    .gallery-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

.gallery-grid img {
    width: 95%;
    height: 95%;
    padding-bottom:10px;
    object-fit: cover;

}

.grid2x2 {
    width:100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    height:95%;
    max-height: 95%;

  }
  .grid2x2 > div {
    display: flex; 
    flex-basis: calc(49%);  
    justify-content: center;
    flex-direction: column;
  }
  .grid2x2 > div > div {
    display: flex;
    justify-content: center;
    flex-direction: row;
  }
  
  .box { margin: 0px; width:50%;height:51%}


  
