.gallery-container {
    /* padding: .9375rem 0; */
}



.gallery-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    max-width: 1200px;
    width: 100%;
    margin: 50px auto;
}

@media (min-width: 20em) {
    .gallery-grid {
        grid-template-columns: repeat(1, 1fr);
    }
}

@media (min-width: 34em) {
    .gallery-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 60em) {
    .gallery-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

.gallery-grid img {
    width: 95%;
    height: 95%;
    padding-bottom:10px;
    object-fit: cover;

}

.grid2x2 {
    width:100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    height:95%;
    max-height: 95%;

  }
  .grid2x2 > div {
    display: flex; 
    flex-basis: calc(49%);  
    justify-content: center;
    flex-direction: column;
  }
  .grid2x2 > div > div {
    display: flex;
    justify-content: center;
    flex-direction: row;
  }
  
  .box { margin: 0px; width:50%;height:51%}


  
